import { DisplayMode } from '@microsoft/sp-client-base';
import { FocusZone } from 'office-ui-fabric-react/lib/FocusZone';
import * as React from 'react';

import CanvasControlType from '../../canvas/canvas/CanvasControlType';
import { IToolbarProps } from './IToolbar';
import ToolbarButton from '../../canvas/canvas/ToolbarButton';

import './Toolbar.scss';
import Strings from '../loc/CanvasStrings.resx';

export default class Toolbar extends React.Component<IToolbarProps, void> {
  constructor(props: IToolbarProps) {
    super(props);

    this._handleConfigureButtonClicked = this._handleConfigureButtonClicked.bind(this);
    this._handleDeleteButtonClicked = this._handleDeleteButtonClicked.bind(this);
  }

  public render(): React.ReactElement<IToolbarProps> {
    let configureButton: JSX.Element;

    if (this.props.controlType === CanvasControlType.WebPartZone) {
      configureButton = (
        <ToolbarButton
          canBeActive={false}
          fabricIconKey='Edit'
          onClick={this._handleConfigureButtonClicked}
          title={Strings.ToolbarConfigButtonTitle}
        />
      );
    }

    return ( this.props.displayMode === DisplayMode.Edit &&
      <FocusZone isCircularNavigation={true}>
        <div className='CanvasControlToolbar' role='toolbar'>
          { configureButton }
          <ToolbarButton
            canBeActive={false}
            fabricIconKey='Delete'
            onClick={this._handleDeleteButtonClicked}
            title={Strings.ToolbarDeleteButtonTitle}
          />
        </div>
      </FocusZone>
    );
  }

  private _handleConfigureButtonClicked(): void {
    this.props.onConfigureButtonClick(this.props.id);
  }

  private _handleDeleteButtonClicked(): void {
    this.props.onDeleteButtonClick(this.props.row);
  }
}